.custom-button {
  position: relative;
  padding: 0.9rem 3rem;
  margin: 1vh 1vw;
  background-color: antiquewhite;
  border: 0;
  border-radius: 20px;
  font-weight: 600;
  font-size: 19px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  width: auto;
  margin: 1em auto;
  cursor: pointer;
}
