.logo {
  padding: 0;
  margin: 0 3vh;
  background: #cfb99b;
  background: linear-gradient(
    to right,
    #cfb99b 0%,
    #ffab7a 25%,
    #ffe14a 50%,
    #ffa51f 75%,
    #cf1512 100%
  );
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
